/* General */

body {
    box-sizing: border-box;
    margin: 0;
    font-family: 'Montserrat', sans-serif;
}

.main {
    width: 100vw;
    min-height: 100vh;
    background-color: #f1f7fb;
}

.btn {
    padding: 5px 10px;
    background-color: #4e6c35;
    color: white;
    width: 90px;
    font-size: 12px;
    font-weight: 100;
    letter-spacing: 3px;
    text-transform: uppercase;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}

.loading {
    width: 100%;
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 72px;
    font-weight: 400;
}

.no-match {
    width: 100%;
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 56px;
    font-weight: 700;
    letter-spacing: 2px;
}

/*  ******** Header ********  */

.navbar {
    width: 100%;
    height: 60px;
    display: flex;
    justify-content: center;
    background-color: white;
    border-bottom: 2px solid #4e6c35;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.nav-container {
    width: 60%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 1.5px;
    text-decoration: none;
    color: #373737;
}

.logo .green {
    font-weight: 600;
    color: #4e6c35;
}

.contents-list {
    padding: 0;
    display: flex;
    list-style: none;
}

.nav-item {
    margin: 0 10px;
    font-size: 14px;
    
}

.nav-item .navlink {
    text-decoration: none;
    color: #2d2d2d;
}

/*  ******** Home ********  */

.body {
    width: 100%;
    display: flex;
    justify-content: center;
}

.body-container {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/*  Search Box  */

.search-box {
    margin: 60px 0 40px 0;
    width: 50%;
    height: 120px;
    background-color: white;
    border-radius: 4px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.search-box .search-title {
    margin: 25px 0 20px 25px;
    color: #4e6c35;
    font-size: 12px;
    font-weight: 700;
    letter-spacing: 1.5px;
}

.search-box form {
    box-sizing: border-box;
}

.search-box .input {
    padding: 14px 10px;
    margin: 0 25px;
    width: 450px;
    height: 20px;
    font-size: 14px;
    box-sizing: border-box;
    background-color: #f1f7fb;
    border: none;
}

.title {
    margin: 20px 0 30px 0;
    letter-spacing: 3px;
}

/*  Card  */

.results-container {
    width: 100%;
    padding: 0 auto;
    display: flex;
    flex-wrap: wrap;
}

.card {
    flex: 30%;
    max-width: 30%;
    height: 500px;
    margin: 20px 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: white;
    border-radius: 4px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.img-container {
    min-height: 60%;
    width: 100%;
    border-radius: 4px 4px 0 0;
}

.img-container img {
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
    border-radius: 4px 4px 0 0;
}

.info-container {
    margin: 20px;
    height: 40%;
    display: flex;
    flex-direction: column;
}

.card-title {
    margin: 0;
    font-size: 20px;
    letter-spacing: 2px;
}

.cocktail-type {
    margin: 7px 0;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 2.5px;
}

.alcohol {
    margin: 7px 0;
    font-size: 14px;
    font-weight: 200;
}

/* ******** About ******** */

.about {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
}

.about-container {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about-title {
    margin: 70px 0 60px 0;
    font-size: 28px;
    font-weight: 700;
    letter-spacing: 3px;
}

.about-info {
    display: flex;
    justify-content: center;
}

.about-text {
    width: 60%;
    margin: 0;
    padding: 0;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 1.5px;
    line-height: 1.8;
}



/* ******** Not Found Page ******** */

.not-found {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
}

.not-found-container {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.not-found-title {
    margin: 70px 0 60px 0;
    font-size: 28px;
    font-weight: 700;
    letter-spacing: 3px;
}

.home-btn {
    width: 160px;
    padding: 10px 0;
    font-size: 16px;
}


/* ******** Individual Cocktails Page ******** */

.cocktail-focus {
    margin-top: 50px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.cocktail-focus p {
    font-size: 36px;
    font-weight: 200;
}
